.body-lr{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
body{
    background-color: #080710 !important;
}
.background{
    width: 430px;
    height: 520px;
    position: absolute;
    transform: translate(-50%,-50%);
    left: 50%;
    top: 50%;
}
.background .shape{
    height: 200px;
    width: 200px;
    position: absolute;
    border-radius: 50%;
}
.shape:first-child{
    background: linear-gradient(#1845ad,#23a2f6) !important;
    left: -80px;
    top: -80px;
}
.shape:last-child{
    background: linear-gradient(#ff512f,#f09819) !important;
    right: -30px;
    bottom: -80px;
}
.form-lr{
    height: 520px;
    width: 400px;
    background-color: rgba(255,255,255,0.13)!important;
    position: absolute;
    transform: translate(-50%,-50%);
    top: 50%;
    left: 50%;
    border-radius: 10px;
    backdrop-filter: blur(10px) !important;
    box-shadow: 0 0 40px rgba(8,7,16,0.6) !important;
    padding: 50px 35px;
}
.form-register{
    height: 630px;
}
.form-lr *{
    font-family: 'Poppins',sans-serif;
    color: #fff;
    letter-spacing: 1px;
    outline: none;
    border: none;
}
.form-lr h3{
    font-size: 32px;
    font-weight: 500;
    line-height: 42px;
    text-align: center;
}
.form-lr label{
    display: block;
    margin-top: 30px;
    font-size: 16px;
    font-weight: 500;
}
.form-lr input{
    display: block;
    height: 40px;
    width: 100%;
    background-color: rgba(255,255,255,0.07) !important;
    color: #e5e5e5 !important;
    border-radius: 3px;
    border-color: transparent;
    padding: 0 10px;
    margin-top: 8px;
    font-size: 14px;
    font-weight: 300;
}
.form-lr input::placeholder{
    color: #e5e5e5 !important;
}

.form-lr button{
    margin-top: 40px;
    width: 100%;
    background-color: #fff !important;
    color: #000 !important;
    padding: 7px 0;
    font-size: 18px;
    font-weight: bold;
    border-radius: 5px;
    cursor: pointer;
}
.social{
    margin-top: 30px;
    display: flex;
}
.form-lr .social div{
    width: 150px;
    border-radius: 3px;
    padding: 5px 10px 10px 5px;
    background-color: rgba(255,255,255,0.27)!important;
    color: #eaf0fb !important;
    text-align: center;
    cursor: pointer;
}
.form-lr .social div:hover{
    background-color: rgba(255,255,255,0.47)!important;
}
.form-lr .social i{
    margin-right: 4px;
}
.form-lr .social .fb{
    margin-left: 25px;
}
